.clients {
    margin-top: 30px;
    margin-bottom: 150px;

    .row {
        justify-content: space-between;
    }



    .slick-track {
        display: flex;
    }
    
    .slick-list {
        overflow: hidden;
    }



    .slick-dots{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .slick-dots li{
        list-style: none;
        margin-right: 10px;
    }

    .slick-dots button{
        font-size: 0;
        width: 20px;
        height: 20px;
        background: white;
        border-radius: 50%;
        outline: none;
        color: white;
        border-color: $black;
    }
    
    .slick-dots li.slick-active button{
        border: none;
        background: black;
    }



}

.clients__title {
    text-align: center;
    font-size: 40px;
    color: $menu-bg;
    margin-bottom: 60px;
    font-weight: 400;

}

.client {
    @include col(2);
    text-align: center;
    margin-bottom: 30px;

    @include media($md) {
        // @include col(4);
        margin-bottom: 40px;
    }

    @include media($xs) {
        @include col(6);
    }

    &__img {
        // width: 100px;   
        height: 50px;   
        // width: 100px;
        // border-radius: 50%;

        // @include media($xs) {
        //     width: 150px;
        // }

    }

    &__title {
        font-weight: 400;
        font-size: 20px;
        margin-top: 20px;
        margin-bottom: 10px;

        @include media($xs) {
            font-size: 23px;
        }
    }

    &__client__info {
        font-size: 13px;
    }
}



