.skills {
    padding: 60px 0 80px 0;
    display: flex;
    // background: red;
    justify-content: space-between;

    
    @include media($md){
        justify-content: center;
    }
    @include media($xs){
        justify-content: center
    }


    &__block-title {
        text-transform: uppercase;
        text-align: center;
        // font-weight: 400;
        margin-top: 30px;
        margin-bottom: 40px;
    }

    &__block {
        @include col(4);
        text-align: center;

        @include media($md) {
            @include col(5);
            margin-bottom: 30px;
        }
        @include media($xs) {
            @include col(10);
            margin-bottom: 60px;
        }
    }


    &__img {
        width: 150px;
        border-radius: 50%;
    }

    &__title {
        font-size: 20px;
        margin-top: 20px;
        margin-bottom: 15px;
    }


}
