.banner {
    width: 100%;
    height: 50vh;
    position: relative;
    z-index: 3;



    .slick-track {
        display: flex;
    }

    .slick-list {
        overflow: hidden;
    }

}

// .dark__fone {
//     height: 100%;
//     background: rgba(0, 0, 0, 0.322);
//     // z-index: 999;
//     position: relative;
// }

.slide__img {
    // position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.banner__block {
    background: $menu-bg;

}

.banner__content {
    text-align: center;
    max-width: 700px;
    margin: auto;
    color: $white;
    padding: 50px 15px 100px 15px;
}

.banner__info {
    font-size: 19px;
    margin-top: 10px;
    margin-bottom: 30px;

    @include media($xs){
        font-size: 15px;
    }
}

.banner__title {
    font-size: 50px;
    text-transform: uppercase;
    font-family: 'Montserrat';

    @include media($xs){
        font-size: 35px;
    }
}

.banner__link {
    color: $white;
    margin-top: 20px;
    display: inline-block;
}


.slide {
    height: 50vh;
    z-index: 100;

    &__img {
        z-index: 1;
    }
}